import { storage, db } from './baas';
import { collection, getDocs, doc, deleteDoc,  getFirestore, query, where } from 'firebase/firestore';

export const getUserProducts = async () => {
    try {
        // Referncia para a coleo 'products'
        const productsRef = collection(db, 'products');
        
        // Obtendo todos os documentos da coleo
        const querySnapshot = await getDocs(productsRef);
        
        // Processando os dados
        let products = querySnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));

        return products; // Retorna os produtos obtidos
      } catch (error) {
        console.error("Erro ao obter produtos:", error);
      }
};

export const getUserProductsByUser = async (username) => {
    let products = await getUserProducts()
    
    products = products.filter((product) => product.username == username);
    return products; 
};

export const getUserProductsByCategory = async (category) => {     
    let products = await getUserProducts()
    products = products.filter((product) => product.category == category);
    return products; 
};

export const getProductById = async (id) => {
  let products = await getUserProducts()
  let product = products.filter((product) => product.id == id);
  return product[0]; 
};

export const getInterestNotificationsByUser = async (username) => {
  const interestNotification = collection(db, 'interestNotification');
        
        // Obtendo todos os documentos da coleo
  const querySnapshot = await getDocs(interestNotification);
        
        // Processando os dados
  let products = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
  }));

  let notifications = products.filter((not) => not.productOwner == username);
  return notifications; 
};

export const deleteNotification = async (notificationId) => {
  try {
    // Obtm a referncia do documento
    const interestNotification = doc(db, "interestNotification", notificationId); // "notifications"  o nome da coleo onde esto as notificaes

    // Deleta o documento
    await deleteDoc(interestNotification);

    console.log("Notificao apagada com sucesso");
  } catch (error) {
    console.error("Erro ao apagar notificao:", error);
  }
};

// Funo para buscar usurio pelo username
export const getUserByUsername = async (username) => {
  try {
    // Obter instncia do Firestore
    const db = getFirestore();
    
    // Referncia  coleo de usurios
    const usersCollection = collection(db, 'users'); // Supondo que a coleo se chama 'users'
    
    // Criar consulta para buscar o usurio pelo username
    const q = query(usersCollection, where('username', '==', username));
    
    // Executar a consulta
    const querySnapshot = await getDocs(q);
    
    // Verificar se o usurio foi encontrado
    if (!querySnapshot.empty) {
      // Retornar o primeiro usurio encontrado
      const userDoc = querySnapshot.docs[0];
      return userDoc.data(); // Retorna os dados do usurio
    } else {
      console.log('Usurio no encontrado');
      return null; // Caso o usurio no seja encontrado
    }
  } catch (error) {
    console.error('Erro ao buscar usurio:', error);
    throw error; // Propaga o erro caso algo acontea
  }
};

export const deleteProductById = async (productId) => {
  try {
    // Referncia ao documento no Firestore
    const productRef = doc(db, "products", productId);
    await deleteDoc(productRef);
    return true;
  } catch (error) {
    console.error("Erro ao excluir o produto:", error);
    return false;
  }
};